import defaultimg from "@/assets/defaultimg.jpg"  
import { useIntersectionObserver } from "@vueuse/core"
export default
{
    // dom 
    // el dom
    // binding 
    mounted(el, binding){
        el.src = defaultimg      
        // console.log(useIntersectionObserver())  
        // isIntersecting 
        let {stop} = useIntersectionObserver(el, ([{isIntersecting}])=>{
            if(isIntersecting){
                el.src = binding.value
                el.onerror=()=>{
                    el.src = defaultimg      
                }
                // 
                stop()
            }
        })    
    },
    updated(el, binding){

    }
}